@import "./src/assets/themes/appdb/variables.scss";

@import "@/assets/variables";

.wrapper {
  display: flex;
  align-items: flex-start;

  @include notebook {
    flex-direction: column;
    align-items: center;
  }

  @media (max-width: 767px) {
    align-items: stretch;
  }
}

.heading {
  display: block;
  width: 100%;
  color: var(--text-tertiary);
  font-size: 20px;
  font-weight: 600;
  margin-bottom: 20px;
  word-break: break-word;

  @include notebook {
    text-align: center;
  }

  @media (max-width: 767px) {
    text-align: left;
  }
}

.filters {
  width: 180px;

  @include notebook {
    width: auto;
  }

  @media (max-width: 767px) {
    width: 100%;
  }
}

.apps {
  margin: 0 20px;
  flex-grow: 1;

  &__loader {
    margin-top: 15px;
  }

  @include notebook {
    margin: 15px 0 0;
  }
}

.not-found {
  text-align: center;

  @include notebook {
    margin-top: 10px;
  }
}

.categories {
  background-image: linear-gradient(180deg, var(--secondary-bg) 0%, var(--bg) 100%);
  border-radius: 10px;
  padding: 20px 15px;
  flex: 0 0 300px;

  @include notebook {
    display: none;
  }

  &__heading {
    color: var(--text-tertiary);
    font-weight: 600;
    font-size: 20px;
    margin-bottom: 25px;
  }
}

.category {
  &:not(:last-child) {
    margin-bottom: 5px;
  }

  &__link {
    display: flex;
    justify-content: space-between;
    padding: 3px 0;
  }

  &__count {
    color: var(--text-primary);
    font-size: 12px;
    font-weight: 400;
  }
}
